<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process236"></a>

<p><br></p>
<p><span class="rvts17">章节探索</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts21">无名岛任何位置</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">探索无名岛</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1317.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">来到无名岛的主要任务是进入神学院。神学院位于月神殿传送点位置，我们需要找到进入神学院的方法。</span>
<br><span class="rvts21">具体有四种：</span>
<br><span class="rvts20">1. </span><span class="rvts21">探索七所祭坛，并获得他们对应的日月派别。（参考</span><span class="rvts20"></span><span class="rvts21">无名岛</span><span class="rvts20"></span><span class="rvts21">支线）</span>
<br><span class="rvts20">2. </span><span class="rvts21">帮助魔导师首领亚历山大，杀掉黑环首领瘦黄男人，他会告诉我们进入神学院的一部分祭坛的日月派别。（参考</span><span class="rvts20"></span><span class="rvts21">一个不太熟悉的主顾（</span><span class="rvts20">An Unlikely Patron</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">支线）</span>
<br><span class="rvts20">3. </span><span class="rvts21">或者帮助黑环首领瘦黄男人，杀掉魔导师首领亚历山大，他会告诉我们进入神学院的一部分祭坛的日月派别。（参考</span><span class="rvts20"></span><span class="rvts21">瘦黄的男人（</span><span class="rvts20">The Sallow man</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">支线）</span>
<br><span class="rvts20">4. </span><span class="rvts21">不求助亚历山大或瘦黄男人，也不用调查祭坛的日月派系，通过秘密通道进入神学院。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1318.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">另外在暗影王子附近，可以找到一处隐藏的山洞，山洞中有可以收集的物品。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1319.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在无名岛北部海滩黑环营地附近，会遇到一处隐藏山洞，山洞中有一把史诗剑。一旦决定拿走它，会激活守卫神剑的石头怪。打败石头怪，会掉落</span><span class="rvts20"></span><span class="rvts21">充能电容</span><span class="rvts20"></span><span class="rvts21">。</span>
<br><span class="rvts21">充能电容可以用于开启学院大门处的电池槽，也可以用于学院内部进入王者竞技场的电池槽。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



